/* APP */
.content-cont{
 padding: 20px;
 display: flex;
 flex-direction: column;
 gap: 10px;
}



.main-content{
  color: var(--white);
  display: flex;
  flex-direction: column;
  border-top: 1px solid rgb(60,60,60);
  padding-top: 10px;
  padding-bottom: 10px;
  padding-left: 15px;
  font-size: 12pt;
}

  .row1-content{
   display: flex;
   align-items: center;
   padding: 5px;
   padding-bottom: 10px;
  }

  .row2-content{
    padding-top: 5px;
    padding-left: 5px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 5px;
  }  

  #profPic-content{
    height: 50px;
    width: 50px;
    margin-top: 5px;
    border-radius: 30px;
   
  } 
  
  .username-content{
    font-size: 12pt;
    font-weight: 700;
    color: cadetblue;
    padding-left: 5px;
    padding-top: 5px;
    text-decoration: none;
  }
    .username-content:hover{
      color: darkcyan;
    }

  #postImage-content{
    width: 200px;
    object-fit: cover;
  }

  .twit-content{
    padding-bottom: 5px;
  }

  .date-content{
    font-size: 8pt;
    opacity: 0.8;
  }

  .retweetBy-content{
    font-size: 10pt;
    padding-top: 5px;
    color: cadetblue;
  }
      #retweet-icon:active {
        color: cadetblue;
        padding: 1px;
      }

  .icon-cont{
    padding-left: 5px;
  }

  .icon-cont>span{
    padding-right: 20px;
    padding-top: 10px;
    font-size: 14pt;
    cursor: pointer;
    color: var(--white);
  }
      
  #reply-icon{
    cursor: pointer;
  }

      #reply-form{
        display: none;
      }
        #reply-header{
          color: cadetblue;
          font-size: 10pt;
        }
        #reply-input{
          resize: none;
        }
        #reply-btn{
          display: block;
          background-color: cadetblue;
          color: white;
          border-radius: 5px;
        }
    
    #reply-display{
      display: block;
      padding-top: 10px;
      padding-left: 10px;
    }
        .reply-display-head{
          color: cadetblue;
        }
        .reply-display-body{
          color: white;
          font-size: 12pt;
          padding-left: 10px;
          border-left: 1px solid rgb(60,60,60);
        }
        .reply-display-body>div{
          padding-bottom: 5px;
        }
          #reply-time{
            font-size: 7pt;
          }
    
