/* ipad size */
@media screen and (max-width: 1100px) {
  .app-tab, .profile-tab{
    grid-template-columns: 2fr 5fr;
  }
  .nav-link{
    display: inline;
  }
  .nav-link>a{
    padding-bottom: 50px;
  }
}

/* mobile size */
@media screen and (max-width: 600px) {
  .app-tab, .profile-tab, .message-tab{
    grid-template-columns: 1fr;
    grid-template-rows: 2fr auto;
  }
  .nav-link{
    display: grid;
    grid-template-columns: repeat(2,1fr);
    grid-template-rows: repeat(2,1fr);
    grid-row: 3/4;
  }
  .nav-link>a{
    padding-bottom: 50px;
  }
  .dashboard{
    height: 500px;
    position: static;
    grid-template-rows: repeat(3,1fr);
  }
  .title-app{
    display: flex;
    align-items: center;
    height: 200px;
    font-size: 30pt;
    padding-top: 0%;
  }
  #new-twit{
    padding: 5px 20px;
  }
  .profile-name{
    position: relative;
    top: 50px;
  }
}
